import { Button } from '@material-ui/core';
import { useData } from 'contexts/Data';
import React from 'react';
import { Subject } from 'types/Subject';
import { areSubjects } from 'types/TypeGuards';

export default function () {
  const { setSubjects } = useData();

  function readFile(fileEvent: React.ChangeEvent<HTMLInputElement>) {
    if (!fileEvent.target.files) return;

    const file: File = fileEvent.target.files[0];

    if (!file.type.endsWith('json')) {
      return;
    }

    const reader = new FileReader();

    reader.onload = readEvent => {
      if (readEvent.target?.result) {
        const newSubjects: Subject[] = JSON.parse(
          readEvent.target.result as string
        );

        areSubjects(newSubjects)
          ? setSubjects(newSubjects)
          : console.log('Invalid file data.');
      }
    };

    reader.readAsText(file);

    // Reset file path, same file can fire event multiple times
    fileEvent.target.value = '';
  }

  return (
    <Button component="label" variant="outlined">
      Input subjects
      <input
        hidden
        accept=".json"
        type="file"
        value={undefined}
        onChange={readFile}
      />
    </Button>
  );
}
