import { Subject } from 'types/Subject';
import { Week } from 'types/Week';
import { Timetable } from 'types/Timetable';
import { Class } from 'types/Class';

type Props = {
  subjects: Subject[];
  selectedTeachers: string[];
  week: Week;
  maxBreaks: number;
  maxPeriods: number;
  minPeriods: number;
};

export default function ({
  subjects,
  selectedTeachers,
  week,
  maxBreaks,
  maxPeriods,
  minPeriods,
}: Props): Timetable[] {
  // All timetables built
  const timetables: Timetable[] = [];

  // Week to checking period availability
  const myWeek = JSON.parse(JSON.stringify(week));

  // Timetable object to build
  const timetable: Timetable = [];

  // Check if class is available
  function classAvailable({ sessions }: Class) {
    sessions.forEach(({ day, start, length, teacher }) => {
      if (selectedTeachers.includes(teacher)) {
        return false;
      }

      for (let i = start; i < start + length; i++) {
        if (myWeek[i - 1][day - 2] !== 0) {
          return false;
        }
      }
    });

    return true;
  }

  // Push/pop class in week
  function checkClassWeek({ sessions }: Class, value: 0 | 2) {
    sessions.forEach(({ day, start, length }) => {
      for (let i = start; i < start + length; i++) {
        myWeek[i - 1][day - 2] = value;
      }
    });
  }

  // Push class to timetable and week
  function pushClass(clazz: Class, { subjectId, subjectName }: Subject) {
    timetable.push({
      subjectId,
      subjectName,
      ...clazz,
    });

    checkClassWeek(clazz, 2);
  }

  // Pop class from timetable and week
  function popClass() {
    const clazz = timetable.pop() as Class;
    checkClassWeek(clazz, 0);
  }

  function isGood() {
    // Days
    for (let day = 0; day < 6; day++) {
      // Total study periods of this day
      let periods = 0;

      // Total breaks of this day
      let breaks = 0;

      // Mark the previous study period
      let previous = -1;

      // Periods
      for (let period = 0; period < 13; period++) {
        // If this period is used for study
        if (myWeek[period][day] === 2) {
          // Increase the number of study periods
          periods++;

          // Check breaks
          if (periods > 1) {
            breaks = Math.max(period - previous - 1, breaks);
          }

          // This study period is "previous" for the next study period
          previous = period;
        }
      }

      if (
        breaks > maxBreaks ||
        (periods > 0 && (periods < minPeriods || periods > maxPeriods))
      ) {
        return false;
      }
    }

    return true;
  }

  function backtrack(i: number) {
    if (i === subjects.length) {
      if (isGood()) {
        timetables.push(JSON.parse(JSON.stringify(timetable)));
      }

      return;
    }

    subjects[i].classes.forEach(clazz => {
      if (classAvailable(clazz)) {
        pushClass(clazz, subjects[i]);

        backtrack(i + 1);

        popClass();
      }
    });
  }

  backtrack(0);

  return timetables;
}
