import { Subject } from 'types/Subject';

export default function (subjects: Subject[]): string[] {
  let teachers: string[] = [];

  subjects.forEach(({ classes }) => {
    classes.forEach(({ sessions }) => {
      sessions.forEach(({ teacher }) => {
        if (!teachers.includes(teacher)) {
          teachers.push(teacher);
        }
      });
    });
  });

  return teachers;
}
