import { Session } from 'types/Session';
import { Class } from 'types/Class';
import { Subject } from 'types/Subject';

export function isNumber(x: any): x is number {
  return typeof x === 'number';
}

export function isString(x: any): x is string {
  return typeof x === 'string';
}

export function isSession(obj: any): obj is Session {
  const { day, start, length, room, teacher } = obj as Session;

  return (
    isNumber(day) &&
    isNumber(start) &&
    isNumber(length) &&
    isString(room) &&
    isString(teacher)
  );
}

export function areSessions(arr: any): arr is Session[] {
  if (!Array.isArray(arr)) {
    return false;
  }

  for (const session of arr as Session[]) {
    if (!isSession(session)) {
      return false;
    }
  }

  return true;
}

export function isClass(obj: any): obj is Class {
  const { group, max, empty, sessions } = obj as Class;

  return (
    isString(group) && isNumber(max) && isNumber(empty) && areSessions(sessions)
  );
}

export function areClasses(arr: any): arr is Class[] {
  if (!Array.isArray(arr)) {
    return false;
  }

  for (const clazz of arr as Class[]) {
    if (!isClass(clazz)) {
      return false;
    }
  }

  return true;
}

export function isSubject(obj: any): obj is Subject {
  const { subjectId, subjectName, credits, classes } = obj as Subject;

  return (
    isString(subjectId) &&
    isString(subjectName) &&
    isNumber(credits) &&
    areClasses(classes)
  );
}

export function areSubjects(arr: any): arr is Subject[] {
  if (!Array.isArray(arr)) {
    return false;
  }

  for (const subject of arr as Class[]) {
    if (!isSubject(subject)) {
      return false;
    }
  }

  return true;
}
