import { useWorker } from '@koale/useworker';
import React, { createContext, useContext, useEffect, useState } from 'react';
import { SetState } from 'types/SetState';
import { Subject } from 'types/Subject';
import { Timetable } from 'types/Timetable';
import { Week } from 'types/Week';
import allTeachersGetter from 'workers/allTeachersGetter';

type ContextType = {
  subjects: Subject[];
  week: Week;
  allTeachers: string[];
  selectedTeachers: string[];
  maxPeriods: number;
  minPeriods: number;
  maxBreaks: number;
  timetables: Timetable[];

  setSubjects: SetState<Subject[]>;
  setSelectedTeachers: SetState<string[]>;
  setMaxPeriods: SetState<number>;
  setMinPeriods: SetState<number>;
  setMaxBreaks: SetState<number>;
  setTimetables: SetState<Timetable[]>;
  setWeek: SetState<Week>;
};

const DataContext = createContext<ContextType | undefined>(undefined);

type Props = {
  children: React.ReactNode;
};

export function DataProvider({ children }: Props) {
  const [subjects, setSubjects] = useState<Subject[]>([]);

  const [week, setWeek] = useState<Week>(
    [...Array(13)].map(() => Array(6).fill(0))
  );

  const [allTeachers, setAllTeachers] = useState<string[]>([]);

  const [selectedTeachers, setSelectedTeachers] = useState<string[]>([]);

  const [maxPeriods, setMaxPeriods] = useState<number>(13);

  const [minPeriods, setMinPeriods] = useState<number>(0);

  const [maxBreaks, setMaxBreaks] = useState<number>(0);

  const [timetables, setTimetables] = useState<Timetable[]>([]);

  const [getAllTeachers] = useWorker(allTeachersGetter);

  useEffect(() => {
    // setAllTeachers(
    //   subjects.reduce((teachers, { classes }) => {
    //     classes.forEach(({ sessions }) => {
    //       sessions.forEach(
    //         ({ teacher }) =>
    //           !teachers.includes(teacher) && teachers.push(teacher)
    //       );
    //     });
    //     return teachers;
    //   }, [] as string[])
    // );

    // async function updateTeachers() {
    //   const allTeachers = await getAllTeachers(subjects);

    //   setAllTeachers(allTeachers);
    //   setSelectedTeachers([]);
    // }

    // updateTeachers();

    getAllTeachers(subjects).then(allTeachers => {
      setAllTeachers(allTeachers);
      setSelectedTeachers([]);
    });
  }, [getAllTeachers, subjects]);

  return (
    <DataContext.Provider
      value={{
        subjects,
        week,
        allTeachers,
        maxPeriods,
        minPeriods,
        maxBreaks,
        selectedTeachers,
        timetables,
        setSubjects,
        setMaxPeriods,
        setMinPeriods,
        setMaxBreaks,
        setSelectedTeachers,
        setWeek,
        setTimetables,
      }}
    >
      {children}
    </DataContext.Provider>
  );
}

export const useData = () => useContext(DataContext) as ContextType;
