import { createMuiTheme } from '@material-ui/core';
import React from 'react';
import styled from 'styled-components';
import MaxBreaksInput from './MaxBreaksInput';
import MaxPeriodsInput from './MaxPeriodsInput';
import MinPeriodsInput from './MinPeriodsInput';
import SubjectsInput from './SubjectsInput';
import TeachersSelect from './TeachersSelect';
import WeekInput, { tableWidth } from './WeekInput';

const theme = createMuiTheme();

const Grid = styled.div`
  display: grid;
  grid-template-columns: 1fr;
  row-gap: ${theme.spacing(3)}px;
`;

const GridItem = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: ${tableWidth}px;
`;

export default function () {
  return (
    <Grid>
      <GridItem>
        <SubjectsInput />
      </GridItem>

      <GridItem>
        <TeachersSelect />
      </GridItem>

      <GridItem>
        Periods to avoid
        <WeekInput />
      </GridItem>

      <GridItem>
        <MaxPeriodsInput />
      </GridItem>

      <GridItem>
        <MinPeriodsInput />
      </GridItem>

      <GridItem>
        <MaxBreaksInput />
      </GridItem>
    </Grid>
  );
}
