import React from 'react';
import { TextField } from '@material-ui/core';
import { useData } from 'contexts/Data';

const inputProps = {
  min: 0,
  max: 13,
};

export default function () {
  const { minPeriods, maxPeriods, setMaxPeriods } = useData();

  function handleChange(event: React.ChangeEvent<HTMLInputElement>) {
    setMaxPeriods(+event.target.value);
  }

  const helperText =
    maxPeriods < minPeriods
      ? 'Max periods must be bigger than or equal to Min periods'
      : '';

  return (
    <TextField
      fullWidth
      error={maxPeriods < minPeriods}
      helperText={helperText}
      inputProps={inputProps}
      label="Max periods per day"
      type="number"
      value={maxPeriods}
      variant="outlined"
      onChange={handleChange}
    />
  );
}
