import { useWorker, WORKER_STATUS } from '@koale/useworker';
import { Button, CircularProgress } from '@material-ui/core';
import { useData } from 'contexts/Data';
import React from 'react';
import styled from 'styled-components';
import timetableBuilder from 'workers/timetableBuilder';

const LoadingWrapper = styled.div`
  position: relative;
`;

const LoadingIcon = styled(CircularProgress).attrs(props => ({
  halfsize: (props.size as number) / 2,
}))`
  position: absolute;
  top: 50%;
  left: 50%;
  margin-top: -${props => props.halfsize}px;
  margin-left: -${props => props.halfsize}px;
`;

type Props = {
  closeDialog: () => void;
};

export default function ({ closeDialog }: Props) {
  const {
    subjects,
    selectedTeachers,
    week,
    maxBreaks,
    maxPeriods,
    minPeriods,
    setTimetables,
  } = useData();

  const [buildTimetables, { status }] = useWorker(timetableBuilder);

  async function handleSubmit() {
    const timetables = await buildTimetables({
      subjects,
      selectedTeachers,
      week,
      maxBreaks,
      maxPeriods,
      minPeriods,
    });

    console.log('Timetables:', timetables);

    setTimetables(timetables);

    closeDialog();
  }

  return (
    <LoadingWrapper>
      <Button
        color="primary"
        disabled={status === WORKER_STATUS.RUNNING}
        onClick={handleSubmit}
      >
        Build timetables
      </Button>
      {status === WORKER_STATUS.RUNNING && <LoadingIcon size={24} />}
    </LoadingWrapper>
  );
}
