import {
  Button,
  Dialog,
  DialogActions,
  DialogContent,
  DialogTitle,
} from '@material-ui/core';
import DataInputForm from 'components/DataInput/Form';
import React, { useState } from 'react';
import SubmitButton from './SubmitButton';
import Title from './Title';

export default function () {
  const [open, setOpen] = useState(false);

  function openDialog() {
    setOpen(true);
  }

  function closeDialog() {
    setOpen(false);
  }

  return (
    <React.Fragment>
      <Button color="primary" variant="contained" onClick={openDialog}>
        Open dialog
      </Button>

      <Dialog open={open} scroll="body" onClose={closeDialog}>
        <DialogTitle disableTypography>
          <Title closeDialog={closeDialog} />
        </DialogTitle>

        <DialogContent dividers>
          <DataInputForm />
        </DialogContent>

        <DialogActions>
          <SubmitButton closeDialog={closeDialog} />
        </DialogActions>
      </Dialog>
    </React.Fragment>
  );
}
