import React, { useState, useEffect } from 'react';
import styled from 'styled-components';
import { useData } from 'contexts/Data';

const Table = styled.table`
  border: 1px solid black;
  border-collapse: collapse;
  table-layout: fixed;
  user-select: none;
`;

const Cell = styled.td`
  width: 144px;
  height: 40px;
  border: 1px solid black;
  vertical-align: top;
  padding: 9px;
`;

type Props = {
  index: number;
};

type SessionData = {
  length: number;
  subjectId: string;
  subjectName: string;
  group: string;
};

type CellData = (string | null | SessionData)[][];

const initialData = (index: number): CellData =>
  [...Array(14)].map((_, period) =>
    [...Array(7)].map((_, day) =>
      !period && day
        ? 'Thứ ' + (day + 1)
        : period && !day
        ? 'Tiết ' + period
        : !period && !day
        ? '' + (index + 1)
        : ''
    )
  );

export default function ({ index }: Props) {
  const [cellData, setCellData] = useState<CellData>(initialData(index));

  const { timetables } = useData();

  useEffect(() => {
    const newCells = initialData(index);

    timetables[index]?.forEach(
      ({ subjectId, subjectName, group, sessions }) => {
        sessions.forEach(({ day, start, length }) => {
          // Cell span
          newCells[start][day - 1] = {
            length,
            subjectId,
            subjectName,
            group,
          };

          // Delete other cells
          for (let i = 1; i < length; i++) {
            newCells[start + i][day - 1] = null;
          }
        });
      }
    );

    setCellData(newCells);
  }, [index, timetables]);

  return (
    <Table>
      <tbody>
        {cellData.map((row, i) => (
          <tr key={i}>
            {row.map(
              (data, j) =>
                data !== null &&
                (typeof data === 'object' ? (
                  <Cell key={j} rowSpan={data.length}>
                    {data.subjectId}
                    <br />
                    {data.subjectName}
                    <br />
                    {'Nhóm ' + data.group}
                  </Cell>
                ) : (
                  <Cell key={j}>{data}</Cell>
                ))
            )}
          </tr>
        ))}
      </tbody>
    </Table>
  );
}
