import React, { useState, useEffect } from 'react';
import Scrollbars from 'react-custom-scrollbars';
import { useData } from 'contexts/Data';
import styled from 'styled-components';
import { Week } from 'types/Week';

const Table = styled.div`
  display: flex;
  flex-direction: column;
  border: solid black;
  border-width: 1px 0 0 1px;
  width: fit-content;
`;

const Row = styled.div`
  display: flex;
`;

const cellSize = 48;

export const tableWidth = cellSize * 7 + 1;

const Cell = styled.div`
  width: ${cellSize}px;
  height: ${cellSize}px;
  display: flex;
  border: solid black;
  border-width: 0 1px 1px 0;
  align-items: center;
  justify-content: center;
  user-select: none;
  text-align: center;
`;

const ScrollbarWrap = styled(Scrollbars)`
  height: ${7 * cellSize}px !important;
`;

export default function () {
  const [mouseDown, setMouseDown] = useState(false);

  const [checkValue, setCheckValue] = useState<0 | 1>(1);

  const { week, setWeek } = useData();

  function updateWeek(period: number, day: number, newValue: 0 | 1) {
    const newWeek: Week = JSON.parse(JSON.stringify(week));
    newWeek[period][day] = newValue;
    setWeek(newWeek);
  }

  useEffect(() => {
    document.body.onmousedown = () => setMouseDown(true);
    document.body.onmouseup = () => setMouseDown(false);

    return () => {
      document.body.onmousedown = null;
      document.body.onmouseup = null;
    };
  }, []);

  function handleMouseDown(period: number, day: number) {
    const newValue = (1 - week[period][day]) as 0 | 1;

    setCheckValue(newValue);

    updateWeek(period, day, newValue);
  }

  function handleMouseOver(period: number, day: number) {
    mouseDown && updateWeek(period, day, checkValue);
  }

  return (
    <Table>
      <Row>
        {/* Top left empty cell */}
        <Cell />

        {/* Day labels */}
        {[...Array(6)].map((_, i) => (
          <Cell key={i}>
            Thứ <br /> {i + 2}
          </Cell>
        ))}
      </Row>

      <ScrollbarWrap>
        {[...Array(13)].map((_, i) => (
          <Row key={i}>
            {/* Period label */}
            <Cell>
              Tiết <br /> {i + 1}
            </Cell>

            {/* Period checkbox cells, shows X when checked */}
            {[...Array(6)].map((_, j) => (
              <Cell
                key={j}
                onMouseDown={() => handleMouseDown(i, j)}
                onMouseOver={() => handleMouseOver(i, j)}
              >
                {!!week[i][j] && '❌'}
              </Cell>
            ))}
          </Row>
        ))}
      </ScrollbarWrap>
    </Table>
  );
}
