import React from 'react';
import { TextField } from '@material-ui/core';
import { useData } from 'contexts/Data';

const inputProps = { min: 0, max: 13 };

export default function MinPeriodsInput() {
  const { maxPeriods, minPeriods, setMinPeriods } = useData();

  function handleChange(event: React.ChangeEvent<HTMLInputElement>) {
    setMinPeriods(+event.target.value);
  }

  const helperText =
    maxPeriods < minPeriods
      ? 'Min periods must be smaller than or equal to Max periods'
      : '';

  return (
    <TextField
      fullWidth
      error={maxPeriods < minPeriods}
      helperText={helperText}
      inputProps={inputProps}
      label="Min periods per day"
      type="number"
      value={minPeriods}
      variant="outlined"
      onChange={handleChange}
    />
  );
}
