import { Checkbox, TextField } from '@material-ui/core';
import CheckBoxIcon from '@material-ui/icons/CheckBox';
import CheckBoxOutlineBlankIcon from '@material-ui/icons/CheckBoxOutlineBlank';
import { Autocomplete, AutocompleteRenderOptionState } from '@material-ui/lab';
import { useData } from 'contexts/Data';
import React from 'react';
import styled from 'styled-components';

const Input = (params: any) => (
  <TextField {...params} label="Teachers to avoid" variant="outlined" />
);

const Icon = <CheckBoxOutlineBlankIcon fontSize="small" />;

const CheckedIcon = <CheckBoxIcon fontSize="small" />;

const OptionCheckbox = styled(Checkbox)`
  margin-right: 8px;
`;

const Option = (option: string, state: AutocompleteRenderOptionState) => (
  <React.Fragment>
    <OptionCheckbox
      checked={state.selected}
      checkedIcon={CheckedIcon}
      icon={Icon}
    />
    {option}
  </React.Fragment>
);

export default function () {
  const { allTeachers, selectedTeachers, setSelectedTeachers } = useData();

  function handleChange(_event: unknown, value: string[]) {
    setSelectedTeachers(value);
  }

  return (
    <Autocomplete
      disableCloseOnSelect
      fullWidth
      multiple
      id="teachers-select"
      noOptionsText="Subjects input required"
      options={allTeachers}
      renderInput={Input}
      renderOption={Option}
      value={selectedTeachers}
      onChange={handleChange}
    />
  );
}
